.heading {
  font-weight: bold;
  color: var(--navy-blue);
  display: flex;
  gap: 1rem;
  text-decoration: none;
}

.heading--h1 {
  font-size: 1.5rem;
  line-height: 35px;
  letter-spacing: -0.333333px;
}

.heading--h2 {
  font-size: 1.25rem;
  line-height: 29px;
  letter-spacing: -0.25px;
}
.heading--h3 {
  font-size: 1.125rem;
  line-height: 26px;
  letter-spacing: -0.25px;
}
.heading--h4 {
  font-size: 0.875rem;
  line-height: 20px;
  letter-spacing: -0.2px;
}

.white {
  color: white;
}
